.feature__head {
  padding: 20px 20px 0;
}

.feature__slogan br {
  display: none;
}

.feature__slogan {
  display: block;
  text-align: center;
  font-size: 24px;
  line-height: 30px;
  font-weight: 100;
  margin: 20px auto 27px;
  max-width: 210px;
}

.feature__wrapper {
  position: relative;
  margin-bottom: 0;
  padding: 50px 0 0;
  text-align: center;
  background: url("../img/bg-triangle-white.svg") no-repeat center -20px;
  background-color: #f2f2f2;
}

.feature__container {
  background-color: #f2f2f2;
}

.feature__list {
  list-style: none;
  text-align: center;
  background-color: #f2f2f2;
  margin: 0;
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 0;
}

.feature__title {
  font-size: 18px;
  color: #000000;
  font-weight: 700;
  margin: 0 0 20px;
}

.feature__discription {
  margin: 0 0 45px;
}

.feature__item:last-child {
  margin-bottom: 0;
}

