@media (min-width: 660px) {
  .feature__head {
    padding: 60px 0;
    text-align: center;
  }

  .feature__slogan {
    font-size: 40px;
    max-width: 600px;
    margin: 0 auto;
    line-height: normal;
  }

  .feature__slogan br {
    display: inline-block;
  }

  .feature__list {
    flex: 0 0 320px;
    padding-left: 50%;
    text-align: left;
    position: relative;
  }

