@media (min-width: 960px) {
  .feature__list {
    margin: 0 auto;
    padding: 0;
    flex-direction: row;
    justify-content: space-between;

    &::before {
      content: "";
      display: none;
    }
  }

  .feature__slogan {
    font-size: 50px;
  }

  .feature__wrapper {
    padding: 120px 0 0;
    background: url("../img/bg-triangle-white.svg") no-repeat center -1px;
    background-color: #f2f2f2;
  }

  .feature__item {
    flex: 0 0 250px;
    margin-top: 0;
    text-align: center;
    font-size: 18px;
    line-height: 30px;

    &::before {
      content: "";
      display: block;
      width: 242px;
      height: 305px;
      margin: 0 auto;
    }
  }

