html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-weight: 300;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
body {
  margin: 0 auto;
  background-color: #ffffff;
  color: #283645;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.page-wrapper {
  margin: 0 auto;
}
@media (min-width: 960px) {
  .page-wrapper {
    width: 960px;
  }
}
.page-header {
  margin-bottom: -70px;
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header__wrapper {
  position: relative;
  padding: 0;
  background-color: #283645;
}
.page-header__logo {
  display: flex;
  align-items: center;
  width: 150px;
  height: 70px;
  padding-left: 20px;
}
.page-header__logo--mobile {
  fill: #ffffff;
}
.page-header__logo--mobile:hover {
  fill: #d22856;
}
.page-header__logo--tablet {
  display: none;
}
.page-header__logo--desktop {
  display: none;
}
@media (min-width: 660px) {
  .page-header__logo {
    height: 110px;
    width: 170px;
    padding-bottom: 14px;
  }
  .page-header__logo--mobile {
    display: none;
  }
  .page-header__logo--tablet {
    display: inline-block;
  }
}
@media (min-width: 960px) {
  .page-header {
    position: relative;
    min-height: 110px;
    margin-bottom: -110px;
    padding: 27px 0 43px;
  }
  .page-header__wrapper {
    display: flex;
    align-items: flex-end;
    background-color: transparent;
  }
  .page-header__logo {
    width: auto;
    margin-right: 20px;
    height: 40px;
    align-items: flex-end;
    padding: 0;
  }
  .page-header__logo--tablet {
    display: none;
  }
  .page-header__logo--desktop {
    display: block;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
}
.main-nav__toggle {
  display: none;
  position: absolute;
  top: 0;
  right: 5px;
  width: 70px;
  height: 70px;
  border: none;
  z-index: 9;
  cursor: pointer;
  outline: none;
  background-color: transparent;
}
.main-nav__toggle:hover svg {
  fill: #d22856;
}
.main-nav__toggle:active svg {
  fill: #d22856;
  opacity: 0.3;
}
.main-nav__toggle svg {
  fill: #ffffff;
}
.main-nav__item {
  border-top: 1px solid rgba(255, 255, 255, 0.15);
}
.main-nav__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  color: #ffffff;
  padding: 23px 25px 22px;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 20px;
  }
}
@media (min-width: 960px) {
  .main-nav {
    flex: 0 0 622px;
    margin-left: auto;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    border: 0;
  }
  .main-nav__item {
    border: 0;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -5px;
    left: 0;
    height: 2px;
    background: #ffffff;
  }
  .main-nav__link {
    min-height: 0;
    padding: 0;
    font-weight: 400;
    line-height: 18px;
    text-transform: none;
  }
  .main-nav__link:hover {
    color: #d22856;
  }
  .main-nav__link::active {
    color: #ffffff;
    opacity: 0.3;
  }
}
